.container {
	display: flex;
	flex-direction: column;
	max-width: 100vw;
	padding: 2rem;
}

.main-container {
	position: fixed;
	top: 5rem;
	width: 100vw;
	overflow-y: auto;
	height: 100%;
	padding: 2rem;
}
.sorting-container{
	position: fixed;
	top: 5rem;
	display: flex;
	z-index: 10;
}

.sorting-container > * {
	display: flex;
    flex-direction: column;
    z-index: 1;
	width: 6rem;
	min-width: fit-content;
	font-size: 1rem;
	margin: 0 0.5rem;
}


.sorting-container > * > button{
	padding: 0.2rem;
	border-radius: 5px;
	border: 1px solid black;
	background-color: inherit;
	cursor: pointer;

}
.country-display-container {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	overflow-y: auto;
	margin-bottom: 5rem;
}

.message-container {
	display: flex;
}
