.search-input-container {
  display: flex;
  flex-direction: column;
  /* width: fit-content; */
  justify-content: center;
  margin: 0 auto;
}

.search-field {
	display: flex;
	justify-content: space-between;
  border: 2px solid black;
  border-radius: 25px;
  width: fit-content;
  padding: 0 1rem;
}

.search-input {
	width: 12rem;
	height: 2.2rem;
	font-size: 1rem;
	/* border: 2px solid #333; */
  background-color: inherit;
  border: none;
	border-radius: 25px;
	padding: 0px;
	/* text-align: center; */
  text-indent: 1rem;
}

.search-input:focus{
  outline: none;
}

input.search-input::placeholder {
	/* text-align: center; */
  /* font-size: 1rem; */
}

.search-button {
  /* padding: 0.4rem; */
  /* padding-right: 1rem; */
  margin-left: 0.2rem;
  border: none;
  background-color: inherit;
	cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
	/* width: 5rem;
	height: 2.1rem;
	font-size: 1rem;
	background-color: #333;
	color: #fff;
	border: none;
	border-radius: 5px;
	padding: 5px;
	margin: 0 5px; */
}

.search-button:focus, .search-button > img:focus{
  outline: none;
}

.search-button > img {
	/* float: left; */
	width: 1.8rem;
	height: 1.8rem;
  background-color: inherit;
  border: none;
}



.search-results {
	flex-direction: column;
	width: 15rem;
	border-radius: 7px;
	font-size: 1rem;
	max-height: 200px;
	overflow-y: auto;
	z-index: 20;
	overflow-x: hidden;
	margin-top: 0.5rem;
}

